body {
  min-width: 320px;
}

section {
  margin-left: auto;
  margin-right: auto;
  max-width: 1440px;
  overflow: hidden;
}

.padding-content {
  padding-left: clamp(20px, 9.5vw, 135px);
  padding-right: clamp(20px, 9.5vw, 135px);
}

@media (min-width: 0px) {
  @media not all and (min-width: 768px) {
    .burger-content {
      position: absolute;
      top: -150%;
      left: 0px;
      height: 100dvh;
      width: 100vw;
      --tw-bg-opacity: 1;
      background-color: rgb(255 255 255 / var(--tw-bg-opacity));
      padding-top: 100px;
      padding-bottom: 100px;
      padding-left: 30px;
      padding-right: 30px;
    }

    .burger-content_active {
      top: 0px;
    }
  }
}

.burger-button_active {
  span:nth-child(1) {
    position: relative;
    top: 50%;
    transform: rotateZ(45deg) translateY(-50%);
  }
  span:nth-child(2) {
    width: 0;
  }
  span:nth-child(3) {
    position: relative;
    top: -50%;
    transform: rotateZ(-45deg) translateY(50%);
  }
}

.burger-menu-line {
  height: 2px;
  width: 100%;
  --tw-bg-opacity: 1;
  background-color: rgb(51 51 51 / var(--tw-bg-opacity));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.intro__title {
  z-index: 20;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: TTB;
  font-size: clamp(1.75rem, 6.3vw, 4rem);
  line-height: 100%;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}

@media (min-width: 0px) {
  @media not all and (min-width: 768px) {
    .intro__title {
      position: absolute;
      margin-bottom: 10px;
      flex-direction: row;
    }
  }
}

.custom-button {
  display: flex;
  height: 71px;
  width: 222px;
  align-items: center;
  justify-content: center;
  --tw-border-opacity: 1;
  border-color: rgb(255 255 255 / var(--tw-border-opacity));
  font-family: TTM;
  font-size: 12px;
  letter-spacing: 0.2em;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}

@media (min-width: 768px) {
  .custom-button {
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  }
}

.custom-button {
  &::after {
    margin-left: 11px;
  }
  &::after {
    display: inline-block;
  }
  &::after {
    height: 24px;
  }
  &::after {
    width: 24px;
  }
  &::after {
    background-image: url('/src/styles/img/arrow-right.svg');
  }
  &::after {
    background-position: center;
  }
  &::after {
    background-repeat: no-repeat;
  }
  &::after {
    --tw-content: "";
    content: var(--tw-content);
  }
  @media (min-width: 0px) {
    @media not all and (min-width: 768px) {
      &::after {
        display: none;
      }
    }
  }
}

